<script>
import { mapState, mapActions, mapMutations } from 'vuex';

export default {
  data() {
    return {
      id: this.$route.params.workId,
      workSearch: '',
      // worksType: [],
      tagsType: [],
    }
  },
  computed: {
    ...mapState(['works']),
    resultWorks() { // tags 篩選功能 and 搜尋有沒有符合輸入到 workSearch 裡面的字，再到 resultWorks 再到頁面上
      if (!this.workSearch && this.tagsType.length === 0) {
        console.log('沒有搜尋')
        return this.works
      } else {
        console.log('正在搜尋')
        return this.works.filter(item => {
          // return item.title.match(this.workSearch) || item.tags.indexOf(this.tagsType)
          const c = this.tagsType.filter(val => {
            const e = item.tags.indexOf(val) > -1 // 大於 -1 就為 true
            console.log(e)
            if (e) return e // e 是 true ，就 return e
          })
          console.log('c', c)
          if (this.workSearch) return item.title.match(this.workSearch) // Search
          if (c.length != 0) return c // c 有值就回傳
        })
      }
      // let result = []
      // this.works.forEach((item, i) => {
      //   const c = this.tagsType.filter((val, n) => {
      //     const e = item.tags.indexOf(val) > -1
      //     console.log(e)
      //     if (e) return e
      //   })
      //   console.log('c', c)
      //   if (c.length != 0) result.push(item)
      // })
      // console.log('result', result)
      // return result
    },
    worksType() { // 攤平找出所有 tags ，再篩掉重複的
      const data = this.works.reduce((acc, val) => acc.concat(val.tags), [])
      return data.filter((item, i) => {
        return data.indexOf(item) === i
      })
    }
  },
  methods: {
    ...mapActions(['getWorks']),
    getWorkPic(item) {
      // return require(`@/assets/images/i_pro_${i + 1}.jpg`)
      return `./images/${item}/1.jpg`
    },
  },
  created() {
    this.getWorks();
  },
}
</script>